	module.exports = {"data":{"markdownRemark":{"html":"<h2>Computer Hardware</h2>\n<p>In this section, we explore the various hardware components of modern computers. </p>\n<h2>Parts of a Personal Computer</h2>\n<h3>Central Processing Unit (CPU)</h3>\n<p>A Central Processing Unit (CPU) is the electronic circuitry within a computer that carries out the instructions of a computer program by performing the basic arithmetic, logical, control and input/output (I/O) operations specified by the instructions.</p>\n<p><img src=\"http://ecx.images-amazon.com/images/I/51MOTjmtg2L.jpg\" alt=\"Intel Core i7\"></p>\n<h3>Expansion Cards (eg. GPU)</h3>\n<p>A graphics processing unit (GPU) is a specialized electronic circuit designed to rapidly manipulate and alter memory to accelerate the creation of images in a frame buffer intended for output to a display device.</p>\n<p><img src=\"https://cdn.arstechnica.net/wp-content/uploads/2017/04/nvidia-titan-x.jpg\" alt=\"Nvidia Titan XP\">  </p>\n<h3>Storage (SSD or HDD)</h3>\n<p>The Solid State Drive (SSD) or Hard Disk Drive (HDD) are used to store information on the computer. It retains the memory even after loss of power.</p>\n<p><img src=\"https://images10.newegg.com/NeweggImage/ProductImage/20-147-373-09.jpg\" alt=\"SSD\">  </p>\n<h3>Memory (RAM)</h3>\n<p>Random-access memory (RAM) is a form of computer data storage which stores data and machine code currently being used. A random-access memory device allows data items to be read or written in almost the same amount of time irrespective of the physical location of data inside the memory.</p>\n<p><img src=\"https://images10.newegg.com/NeweggImage/ProductImage/20-232-476-S01.jpg\" alt=\"Ram\">  </p>\n<h3>Case</h3>\n<p>A case is used to house all the components of a computer.</p>\n<p><img src=\"https://images-na.ssl-images-amazon.com/images/I/41Wq97Jk20L._SL500_AC_SS350_.jpg\" alt=\"Case\">  </p>\n<h3>Power Supply</h3>\n<p>A power supply is an electronic device that supplies electric energy to an electrical load.</p>\n<p> <img src=\"http://www.corsair.com/~/media/corsair/product%20photos/psu/ax-series/ax1200i/large/axi_psu_sideview_a.png\" alt=\"Power Supply\"></p>\n<h3>Motherboard</h3>\n<p>Motherboard holds and allows communication between many of the crucial electronic components of a system, such as the central processing unit (CPU) and memory, and provides connectors for other peripherals.</p>\n<p><img src=\"https://fthmb.tqn.com/mmTUdCN6UBGIKlkEggGq4fpV-3k=/768x0/filters:no_upscale()/gig-57c732ed3df78c71b60e7aa5.jpg\" alt=\"Motherboard\">  </p>\n<h3>Input and Output devices (eg. Keyboard and Mouse)</h3>\n<p>Input/Output device is any hardware used by a human operator or other systems to communicate with a computer.</p>\n<p><img src=\"https://www.clasohlson.com/medias/sys_master/9225929949214.jpg\" alt=\"I/O Devices\"></p>\n<h3>Monitor</h3>\n<p>A computer monitor is an output device which displays information in pictorial form.</p>\n<p><img src=\"https://assets.pcmag.com/media/images/523934-dell-ultrasharp-34-curved-monitor-u3417w.jpg?thumb=y\" alt=\"Monitor\"></p>","fields":{"slug":"/computer-hardware/"},"frontmatter":{"title":"Computer Hardware"}}},"pathContext":{"slug":"/computer-hardware/"}}

